// RxJS
import { of } from "rxjs";
import { catchError, finalize, tap, debounceTime, delay, distinctUntilChanged } from "rxjs/operators";
// NGRX
import { Store, select } from "@ngrx/store";
// CRUD
import { BaseDataSource, QueryResultsModel } from "../_base/crud";
// State
import { AppState } from "../../core/reducers";
import { selectOnlineOrdersInStore, selectOnlineOrdersPageLoading, selectOnlineOrdersShowInitWaitingMessage } from "./onlineOrders.selector";

export class OnlineOrdersDataSource extends BaseDataSource {
	constructor(private store: Store<AppState>) {
		super();

		this.loading$ = this.store.pipe(select(selectOnlineOrdersPageLoading));

		this.isPreloadTextViewed$ = this.store.pipe(select(selectOnlineOrdersShowInitWaitingMessage));

		this.store.pipe(select(selectOnlineOrdersInStore)).subscribe((response: QueryResultsModel) => {
			this.paginatorTotalSubject.next(response.totalCount);
			this.entitySubject.next(response.items);
		});
	}
}
