// NGRX
import { Action } from "@ngrx/store";
import { Update } from "@ngrx/entity";
// CRUD
import { OnlineOrdersModel } from "./onlineOrders.model";
import { QueryOnlineOrdersModel } from "./queryonlineOrders.model";
// Models

export enum OnlineOrdersActionTypes {
	AllUsersRequested = "[OnlineOrders Module] All OnlineOrders Requested",
	AllUsersLoaded = "[OnlineOrders API] All OnlineOrders Loaded",
	OnlineOrdersOnServerCreated = "[Edit OnlineOrders Component] OnlineOrders On Server Created",
	OnlineOrdersCreated = "[Edit OnlineOrders Dialog] OnlineOrders Created",
	OnlineOrdersUpdated = "[Edit OnlineOrders Dialog] OnlineOrders Updated",
	OnlineOrdersDeleted = "[OnlineOrders List Page] OnlineOrders Deleted",
	OnlineOrdersPageRequested = "[OnlineOrders List Page] OnlineOrders Page Requested",
	OnlineOrdersPageLoaded = "[OnlineOrders API] OnlineOrders Page Loaded",
	OnlineOrdersPageCancelled = "[OnlineOrders API] OnlineOrders Page Cancelled",
	OnlineOrdersPageToggleLoading = "[OnlineOrders] OnlineOrders Page Toggle Loading",
	OnlineOrdersActionToggleLoading = "[OnlineOrders] OnlineOrders Action Toggle Loading",
}
export class OnlineOrdersOnServerCreated implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersOnServerCreated;
	constructor(public payload: { onlineOrders: OnlineOrdersModel }) {}
}

export class OnlineOrdersCreated implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersCreated;
	constructor(public payload: { onlineOrders: OnlineOrdersModel }) {}
}

export class OnlineOrdersUpdated implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersUpdated;
	constructor(
		public payload: {
			partialOnlineOrders: Update<OnlineOrdersModel>;
			onlineOrders: OnlineOrdersModel;
		}
	) {}
}

export class OnlineOrdersDeleted implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersDeleted;

	constructor(public payload: { id: string }) {}
}

export class OnlineOrdersPageRequested implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersPageRequested;
	constructor(public payload: { page: QueryOnlineOrdersModel }) {}
}

export class OnlineOrdersPageLoaded implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersPageLoaded;
	constructor(public payload: { onlineOrders: OnlineOrdersModel[]; totalCount: number; page: QueryOnlineOrdersModel }) {}
}

export class OnlineOrdersPageCancelled implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersPageCancelled;
}

export class OnlineOrdersPageToggleLoading implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersPageToggleLoading;
	constructor(public payload: { isLoading: boolean }) {}
}

export class OnlineOrdersActionToggleLoading implements Action {
	readonly type = OnlineOrdersActionTypes.OnlineOrdersActionToggleLoading;
	constructor(public payload: { isLoading: boolean }) {}
}

export type OnlineOrdersActions = OnlineOrdersCreated | OnlineOrdersUpdated | OnlineOrdersDeleted | OnlineOrdersOnServerCreated | OnlineOrdersPageLoaded | OnlineOrdersPageCancelled | OnlineOrdersPageToggleLoading | OnlineOrdersPageRequested | OnlineOrdersActionToggleLoading;
