// RxJS
import { BehaviorSubject, Observable, of, Subscription } from "rxjs";
// NGRX
import { Store, select } from "@ngrx/store";
import { Update } from "@ngrx/entity";
import { AppState } from "../../../../core/reducers";
// Layout
import { SubheaderService, LayoutConfigService } from "../../../../core/_base/layout";
import { LayoutUtilsService, MessageType } from "../../../../core/_base/crud";
import { OnlineOrdersModel } from "../../../../core/onlineOrders/onlineOrders.model";
import { selectLastCreatedOnlineOrdersId, selectOnlineOrdersActionLoading, selectOnlineOrdersById } from "../../../../core/onlineOrders/onlineOrders.selector";
import { OnlineOrdersService } from "../../../../core/onlineOrders/onlineOrders.service";

@Component({
	selector: "kt-add-onlineOrders",
	templateUrl: "./add-onlineOrders.component.html",
	styleUrls: ["./add-onlineOrders.component.scss"],
})
export class AddOnlineOrdersComponent implements OnInit, OnDestroy {
	// Public properties
	codenum: any;
	onlineOrders: OnlineOrdersModel;
	OnlineOrdersId$: Observable<string>;
	oldOnlineOrders: OnlineOrdersModel;
	selectedTab = 0;
	loading$: Observable<boolean>;
	onlineOrdersForm: FormGroup;
	date1 = new FormControl(new Date());
	hasFormErrors = false;
	checker: boolean;
	loading: boolean;

	onlineOrdersName: string = "";
	faqs = [];
	features = [];
	selectedFeature: any[] = [];
	// Private properties
	private subscriptions: Subscription[] = [];
	constructor(private activatedRoute: ActivatedRoute, private router: Router, private onlineOrdersFB: FormBuilder, private subheaderService: SubheaderService, private layoutUtilsService: LayoutUtilsService, private store: Store<AppState>, private service: OnlineOrdersService, private layoutConfigService: LayoutConfigService) {}

	ngOnInit() {
		this.loading$ = this.store.pipe(select(selectOnlineOrdersActionLoading));
		const routeSubscription = this.activatedRoute.params.subscribe((params) => {
			const id = params.id;
			if (id) {
				this.store.pipe(select(selectOnlineOrdersById(id))).subscribe((res) => {
					if (res) {
						this.onlineOrders = res;
						this.oldOnlineOrders = Object.assign({}, this.onlineOrders);
					}
				});
			} else {
				this.onlineOrders = new OnlineOrdersModel();
				this.onlineOrders.clear();
			}

			this.createForm();
		});
		this.subscriptions.push(routeSubscription);
	}

	createForm() {
		if (this.onlineOrders._id) {
			this.onlineOrdersForm = this.onlineOrdersFB.group({
				name: [this.onlineOrders.name, Validators.required],
				type: [this.onlineOrders.type, Validators.required],
				time: [this.onlineOrders.time, Validators.required],
				price: [this.onlineOrders.price, Validators.required],
				price_float: [this.onlineOrders.price_float],
			});
		} else {
			// this.getNumber();
			this.onlineOrdersForm = this.onlineOrdersFB.group({
				name: ["", Validators.required],
				type: ["", Validators.required],
				time: ["", Validators.required],
				price: ["", Validators.required],
				price_float: ["", Validators.required],
			});
		}
	}

	goBackWithId() {
		const url = `/onlineOrders`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	refreshOnlineOrders(isNew: boolean = false, id: string = "") {
		let url = this.router.url;
		if (!isNew) {
			this.router.navigate([url], { relativeTo: this.activatedRoute });
			return;
		}

		url = `/onlineOrders/edit/${id}`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	onSubmit(withBack: boolean = false) {
		this.hasFormErrors = false;
		const controls = this.onlineOrdersForm.controls;
		/** check form */
		if (this.onlineOrdersForm.invalid) {
			Object.keys(controls).forEach((controlName) => controls[controlName].markAsTouched());

			this.hasFormErrors = true;
			this.selectedTab = 0;
			return;
		}

		this.loading = true;
		const editedOnlineOrders = this.prepareOnlineOrders();

		if (this.faqs.length === 0) {
			const message = `Menu not detected, please refresh page`;
			this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);

			return;
		}

		if (editedOnlineOrders._id) {
			this.updateOnlineOrders(editedOnlineOrders, withBack);
			return;
		}

		this.addOnlineOrders(editedOnlineOrders, withBack);
	}
	prepareOnlineOrders(): OnlineOrdersModel {
		const controls = this.onlineOrdersForm.controls;
		const _onlineOrders = new OnlineOrdersModel();
		_onlineOrders.clear();
		_onlineOrders._id = this.onlineOrders._id;
		_onlineOrders.name = controls.name.value;
		_onlineOrders.type = controls.type.value;
		_onlineOrders.time = controls.time.value;
		_onlineOrders.price = controls.price.value;
		return _onlineOrders;
	}

	addOnlineOrders(_onlineOrders: OnlineOrdersModel, withBack: boolean = false) {
		const addSubscription = this.service.createOnlineOrders(_onlineOrders).subscribe(
			(res) => {
				const message = `New OnlineOrders successfully has been added.`;
				this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);
				const url = `/onlineOrders`;
				this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
			},
			(err) => {
				console.error(err);
				const message = "Error while adding onlineOrders | " + err.statusText;
				this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, false);
			}
		);
		this.subscriptions.push(addSubscription);
	}

	updateOnlineOrders(_onlineOrders: OnlineOrdersModel, withBack: boolean = false) {
		// Update User
		// tslint:disable-next-line:prefer-const
		const addSubscription = this.service.updateOnlineOrders(_onlineOrders).subscribe(
			(res) => {
				const message = `OnlineOrders successfully has been saved.`;
				this.layoutUtilsService.showActionNotification(message, MessageType.Update, 5000, true, true);
				const url = `/onlineOrders`;
				this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
			},
			(err) => {
				console.error(err);
				const message = "Error while adding onlineOrders | " + err.statusText;
				this.layoutUtilsService.showActionNotification(message, MessageType.Update, 5000, true, false);
			}
		);
		this.subscriptions.push(addSubscription);
	}

	getComponentTitle() {
		let result = "Buat Pesanan Online";
		if (!this.onlineOrders || !this.onlineOrders._id) {
			return result;
		}

		result = `Edit OnlineOrders`;
		return result;
	}

	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	ngOnDestroy() {
		this.subscriptions.forEach((sb) => sb.unsubscribe());
	}
	/**
	 * toggleSubMenu
	 * @param item
	 */
	toggleSubMenu(item: any) {
		item.showSubMenu = !item.showSubMenu;
	}

	/**
	 * toggleNestedSubMenu
	 * @param parentSubMenu
	 * @param index
	 */
	toggleNestedSubMenu(parentSubMenu: any, index: number) {
		parentSubMenu.submenu[index].showSubMenu = !parentSubMenu.submenu[index].showSubMenu;
	}

	/**
	 * toggleNestedSubMenu
	 * @param parentSubMenu
	 * @param index
	 */
	toggleNestedSubMenu2(parentSubMenu: any, index: number, indexNested: number) {
		parentSubMenu.submenu[index].submenu[indexNested].showSubMenu = !parentSubMenu.submenu[index].submenu[indexNested].showSubMenu;
	}

	/**
	 * handleOnlineOrdersNameChange
	 * @param event
	 */
	handleOnlineOrdersNameChange(event: any): void {
		this.onlineOrdersName = event.target.value;
	}

	/**
	 * shouldCheckboxBeChecked
	 * @param faq
	 * @returns
	 */
	shouldCheckboxBeChecked(faq: any): boolean {
		if (this.onlineOrders._id) {
			// return faq.onlineOrders.includes(this.onlineOrders.onlineOrders);
		} else return false;
	}

	/**
	 * shouldFeatureCheckboxBeChecked
	 * @param faq
	 * @returns
	 */
	shouldFeatureCheckboxBeChecked(featureName: string, type: string): boolean {
		const controls = this.onlineOrdersForm.controls;
		const onlineOrdersName = controls.onlineOrders.value;

		const findSelectedFeature = this.features.find((data) => data.name === featureName);
		const findSelectedFeatureData = this.selectedFeature.find((data) => data.name === featureName);

		if (findSelectedFeatureData) {
			const findOnlineOrdersAccess = findSelectedFeatureData.onlineOrders_access.find((data) => data.onlineOrders === onlineOrdersName);

			if (findOnlineOrdersAccess) {
				if (type === "add") return findOnlineOrdersAccess.add;
				else if (type === "edit") return findOnlineOrdersAccess.edit;
				else if (type === "view") return findOnlineOrdersAccess.view;
				else if (type === "delete") return findOnlineOrdersAccess.delete;
				else if (type === "full-access") return findOnlineOrdersAccess.fullAccess;
				else return false;
			} else return false;
		} else if (findSelectedFeature) {
			const findOnlineOrdersAccess = findSelectedFeature.onlineOrders_access.find((data) => data.onlineOrders === onlineOrdersName);

			if (findOnlineOrdersAccess) {
				if (type === "add") return findOnlineOrdersAccess.add;
				else if (type === "edit") return findOnlineOrdersAccess.edit;
				else if (type === "view") return findOnlineOrdersAccess.view;
				else if (type === "delete") return findOnlineOrdersAccess.delete;
				else if (type === "full-access") return findOnlineOrdersAccess.fullAccess;
				else return false;
			} else return false;
		} else return false;
	}

	/**
	 * handleCheckboxDepth1
	 * @param event
	 * @param faqIndex
	 */
	handleCheckboxDepth1(event: any, faqIndex: number): void {
		const controls = this.onlineOrdersForm.controls;
		if (event.checked) {
			this.addOnlineOrdersToHierarchy(this.faqs[faqIndex], controls.onlineOrders.value);
		} else {
			this.removeOnlineOrdersFromHierarchy(this.faqs[faqIndex], controls.onlineOrders.value);
		}
	}

	/**
	 * handleCheckboxDepth2
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 */
	handleCheckboxDepth2(event: any, faqIndex: number, submenuIndex: number): void {
		const controls = this.onlineOrdersForm.controls;
		if (event.checked) {
			this.addOnlineOrdersToHierarchy(this.faqs[faqIndex].submenu[submenuIndex], controls.onlineOrders.value);
		} else {
			this.removeOnlineOrdersFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex], controls.onlineOrders.value);
		}
	}

	/**
	 * handleCheckboxDepth3
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 * @param nestedIndex
	 */
	handleCheckboxDepth3(event: any, faqIndex: number, submenuIndex: number, nestedIndex: number): void {
		const controls = this.onlineOrdersForm.controls;
		if (event.checked) {
			this.addOnlineOrdersToHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex], controls.onlineOrders.value);
		} else {
			this.removeOnlineOrdersFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex], controls.onlineOrders.value);
		}
	}

	/**
	 * handleCheckboxDepth4
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 * @param nestedIndex
	 */
	handleCheckboxDepth4(event: any, faqIndex: number, submenuIndex: number, nestedIndex: number, nestedIndexAgain: number): void {
		const controls = this.onlineOrdersForm.controls;
		if (event.checked) {
			this.addOnlineOrdersToHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex].submenu[nestedIndexAgain], controls.onlineOrders.value);
		} else {
			this.removeOnlineOrdersFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex].submenu[nestedIndexAgain], controls.onlineOrders.value);
		}
	}

	/**
	 * handleCheckboxFeatureAccess
	 * @param event
	 * @param feature
	 * @param accessType
	 */
	handleCheckboxFeatureAccess(event: any, feature: string, accessType: string): void {
		// Find the index of the feature in selectedFeature array
		const controls = this.onlineOrdersForm.controls;
		const index = this.selectedFeature.findIndex((item) => item.name === feature);

		if (event.checked) {
			// If checked, add or update the feature in selectedFeature array
			if (index !== -1) {
				// Update the specific access type
				if (accessType === "full-access") {
					this.selectedFeature[index].onlineOrders_access[0]["add"] = true;
					this.selectedFeature[index].onlineOrders_access[0]["edit"] = true;
					this.selectedFeature[index].onlineOrders_access[0]["view"] = true;
					this.selectedFeature[index].onlineOrders_access[0]["delete"] = true;
					this.selectedFeature[index].onlineOrders_access[0]["fullAccess"] = true;
				} else {
					this.selectedFeature[index].onlineOrders_access[0][accessType] = true;
				}
			} else if (accessType === "full-access") {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindOnlineOrdersAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findOnlineOrdersAccess = findFeatureAccess.onlineOrders_access.find((data) => data.onlineOrders == controls.onlineOrders.value);
					if (findOnlineOrdersAccess) dataFindOnlineOrdersAccess = findOnlineOrdersAccess;
				}
				const newFeature = {
					name: feature,
					onlineOrders_access: [
						{
							onlineOrders: controls.onlineOrders.value,
							edit: true,
							view: true,
							add: true,
							delete: true,
							fullAccess: true,
						},
					],
				};
				newFeature.onlineOrders_access[0]["fullAccess"] = true;
				this.selectedFeature.push(newFeature);
			} else {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindOnlineOrdersAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findOnlineOrdersAccess = findFeatureAccess.onlineOrders_access.find((data) => data.onlineOrders == controls.onlineOrders.value);
					if (findOnlineOrdersAccess) dataFindOnlineOrdersAccess = findOnlineOrdersAccess;
				}
				const newFeature = {
					name: feature,
					onlineOrders_access: [
						{
							onlineOrders: controls.onlineOrders.value,
							edit: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.edit : false,
							view: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.view : false,
							add: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.add : false,
							delete: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.delete : false,
							fullAccess: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.fullAccess : false,
						},
					],
				};
				newFeature.onlineOrders_access[0][accessType] = true;
				this.selectedFeature.push(newFeature);
			}
		} else {
			// If unchecked, update the specific access type to false or remove the feature from selectedFeature array
			if (index !== -1) {
				// Update the specific access type
				if (accessType === "full-access") {
					this.selectedFeature[index].onlineOrders_access[0]["add"] = false;
					this.selectedFeature[index].onlineOrders_access[0]["edit"] = false;
					this.selectedFeature[index].onlineOrders_access[0]["view"] = false;
					this.selectedFeature[index].onlineOrders_access[0]["delete"] = false;
					this.selectedFeature[index].onlineOrders_access[0]["fullAccess"] = false;
				} else {
					this.selectedFeature[index].onlineOrders_access[0][accessType] = false;
				}
			} else if (accessType === "full-access") {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindOnlineOrdersAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findOnlineOrdersAccess = findFeatureAccess.onlineOrders_access.find((data) => data.onlineOrders == controls.onlineOrders.value);
					if (findOnlineOrdersAccess) dataFindOnlineOrdersAccess = findOnlineOrdersAccess;
				}
				const newFeature = {
					name: feature,
					onlineOrders_access: [
						{
							onlineOrders: controls.onlineOrders.value,
							edit: false,
							view: false,
							add: false,
							delete: false,
							fullAccess: false,
						},
					],
				};
				newFeature.onlineOrders_access[0]["fullAccess"] = false;
				this.selectedFeature.push(newFeature);
			} else {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindOnlineOrdersAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findOnlineOrdersAccess = findFeatureAccess.onlineOrders_access.find((data) => data.onlineOrders == controls.onlineOrders.value);
					if (findOnlineOrdersAccess) dataFindOnlineOrdersAccess = findOnlineOrdersAccess;
				}
				const newFeature = {
					name: feature,
					onlineOrders_access: [
						{
							onlineOrders: controls.onlineOrders.value,
							edit: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.edit : false,
							view: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.view : false,
							add: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.add : false,
							delete: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.delete : false,
							fullAccess: dataFindOnlineOrdersAccess ? dataFindOnlineOrdersAccess.fullAccess : false,
						},
					],
				};
				newFeature.onlineOrders_access[0][accessType] = false;
				this.selectedFeature.push(newFeature);
			}
		}
	}

	/**
	 * changeAmount
	 * @param event
	 * @param id
	 */
	changeAmount(event: any) {
		this.toCurrency(event);
	}

	/**
	 * toCurrency
	 * @param event
	 * @param id
	 * @returns
	 */
	toCurrency(event: any) {
		const controls = this.onlineOrdersForm.controls;

		let value = event.target.value;

		let number_string = value.replace(/[^-\d,]/g, "").toString(),
			isNegative = false;

		if (number_string.startsWith("-")) {
			isNegative = true;
			number_string = number_string.substr(1);
		}

		let split = number_string.split(","),
			sisa = split[0].length % 3,
			rupiah = split[0].substr(0, sisa),
			ribuan = split[0].substr(sisa).match(/\d{3}/gi);

		// tambahkan titik jika yang di input sudah menjadi value ribuan
		let separator;
		if (ribuan) {
			separator = sisa ? "." : "";
			rupiah += separator + ribuan.join(".");
		}

		if (split.length > 1) {
			rupiah += "," + split[1];
		}

		if (isNegative) {
			rupiah = "-" + rupiah;
		}

		controls.price.setValue(rupiah);
		controls.price_float.setValue(rupiah);

		return rupiah;
	}

	// Helper function to add a onlineOrders to the hierarchy
	private addOnlineOrdersToHierarchy(item: any, onlineOrders: string): void {
		if (item.onlineOrders.indexOf(onlineOrders) === -1) {
			item.onlineOrders.push(onlineOrders);
		}
	}

	// Helper function to remove a onlineOrders from the hierarchy
	private removeOnlineOrdersFromHierarchy(item: any, onlineOrders: string): void {
		const onlineOrdersIndex = item.onlineOrders.indexOf(onlineOrders);
		if (onlineOrdersIndex !== -1) {
			item.onlineOrders.splice(onlineOrdersIndex, 1);
		}
	}
}
