// Angular
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { RouterModule, Routes } from "@angular/router";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
// NGRX
import { StoreModule } from "@ngrx/store";
import { EffectsModule } from "@ngrx/effects";
// Translate
import { TranslateModule } from "@ngx-translate/core";
import { PartialsModule } from "../../partials/partials.module";
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService } from "../../../core/_base/crud";
// Shared
import { ActionNotificationComponent } from "../../partials/content/crud";
// Components
import { OnlineOrdersComponent } from "./onlineOrders.component";
import { ListUnittypeComponent } from "./list-onlineOrders/list-onlineOrders.component";
import { AddOnlineOrdersComponent } from "./add-onlineOrders/add-onlineOrders.component";
import { ViewOnlineOrdersComponent } from "./view-onlineOrders/view-onlineOrders.component";

// Material
import { MatInputModule, MatPaginatorModule, MatProgressSpinnerModule, MatSortModule, MatTableModule, MatSelectModule, MatMenuModule, MatProgressBarModule, MatButtonModule, MatCheckboxModule, MatDialogModule, MatTabsModule, MatNativeDateModule, MatCardModule, MatRadioModule, MatIconModule, MatDatepickerModule, MatExpansionModule, MatAutocompleteModule, MAT_DIALOG_DEFAULT_OPTIONS, MatSnackBarModule, MatTooltipModule } from "@angular/material";

import { onlineOrdersReducer } from "../../../core/onlineOrders/onlineOrders.reducer";
import { OnlineOrdersEffect } from "../../../core/onlineOrders/onlineOrders.effect";

const routes: Routes = [
	{
		path: "",
		component: OnlineOrdersComponent,
		children: [
			{
				path: "",
				component: ListUnittypeComponent,
			},
			{
				path: "add",
				component: AddOnlineOrdersComponent,
			},
			{
				path: "edit/:id",
				component: AddOnlineOrdersComponent,
			},
			{
				path: "view/:id",
				component: ViewOnlineOrdersComponent,
			},
		],
	},
];

@NgModule({
	imports: [MatExpansionModule, CommonModule, HttpClientModule, PartialsModule, RouterModule.forChild(routes), StoreModule.forFeature("onlineOrders", onlineOrdersReducer), EffectsModule.forFeature([OnlineOrdersEffect]), FormsModule, ReactiveFormsModule, TranslateModule.forChild(), MatButtonModule, MatMenuModule, MatSelectModule, MatInputModule, MatTableModule, MatAutocompleteModule, MatRadioModule, MatIconModule, MatNativeDateModule, MatProgressBarModule, MatDatepickerModule, MatCardModule, MatPaginatorModule, MatSortModule, MatCheckboxModule, MatProgressSpinnerModule, MatSnackBarModule, MatTabsModule, MatTooltipModule, MatDialogModule],
	providers: [
		InterceptService,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: InterceptService,
			multi: true,
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: "kt-mat-dialog-container__wrapper",
				height: "auto",
				width: "900px",
			},
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
	],
	entryComponents: [ActionNotificationComponent, OnlineOrdersComponent],
	declarations: [OnlineOrdersComponent, ListUnittypeComponent, AddOnlineOrdersComponent, ViewOnlineOrdersComponent],
})
export class OnlineOrdersModule {}
