import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, of } from "rxjs";
import { OnlineOrdersModel } from "./onlineOrders.model";
import { catchError, map } from "rxjs/operators";
import { environment } from "../../../environments/environment";
import { QueryResultsModel } from "../_base/crud";

import * as FileSaver from "file-saver";
import { QueryOnlineOrdersModel } from "./queryonlineOrders.model";

const API_FLOOR_URL = `http://localhost:3000/api/onlineOrders`;
const API_CSV = `${environment.baseAPI}/api/excel/onlineOrders/export`;

@Injectable({
	providedIn: "root",
})
export class OnlineOrdersService {
	constructor(private http: HttpClient) {}
	// get list block group
	getListOnlineOrders(queryParams: QueryOnlineOrdersModel): Observable<QueryResultsModel> {
		const httpHeaders = new HttpHeaders();
		httpHeaders.set("Content-Type", "application/json");
		let options = {
			param: JSON.stringify(queryParams),
		};
		let params = new URLSearchParams();
		for (let key in options) {
			params.set(key, options[key]);
		}
		return this.http.get<QueryResultsModel>(API_FLOOR_URL + "/list?" + params, { headers: httpHeaders });
	}

	findOnlineOrdersById(_id: string): Observable<any> {
		return this.http.get<any>(`${API_FLOOR_URL}/${_id}`);
	}
	deleteOnlineOrders(onlineOrdersId: string) {
		const url = `${API_FLOOR_URL}/delete/${onlineOrdersId}`;
		return this.http.delete(url);
	}
	updateOnlineOrders(onlineOrders: OnlineOrdersModel) {
		const url = `${API_FLOOR_URL}/edit/${onlineOrders._id}`;
		return this.http.patch(url, onlineOrders);
	}
	createOnlineOrders(onlineOrders: OnlineOrdersModel): Observable<OnlineOrdersModel> {
		const httpHeaders = new HttpHeaders();
		httpHeaders.set("Content-Type", "application/json");
		return this.http.post<OnlineOrdersModel>(`${API_FLOOR_URL}/create`, onlineOrders, { headers: httpHeaders });
	}

	private handleError<T>(operation = "operation", result?: any) {
		return (error: any): Observable<any> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead
			// Let the app keep running by returning an empty result.
			return of(result);
		};
	}

	generateOnlineOrdersCode(): Observable<QueryResultsModel> {
		const url = `${API_FLOOR_URL}/generate/code`;
		return this.http.get<QueryResultsModel>(url);
	}

	exportExcel() {
		return FileSaver.saveAs(`${API_CSV}`, "export-onlineOrders.xlsx");
	}
}
