// Angular
import { Injectable } from "@angular/core";
// RxJS
import { mergeMap, map, tap } from "rxjs/operators";
import { Observable, defer, of, forkJoin } from "rxjs";
// NGRX
import { Effect, Actions, ofType } from "@ngrx/effects";
import { Store, select, Action } from "@ngrx/store";
// CRUD
import { QueryResultsModel } from "../_base/crud";
// Services
import { OnlineOrdersService } from "./onlineOrders.service";
// State
import { AppState } from "../../core/reducers";
import { OnlineOrdersActionTypes, OnlineOrdersPageRequested, OnlineOrdersPageLoaded, OnlineOrdersCreated, OnlineOrdersDeleted, OnlineOrdersUpdated, OnlineOrdersOnServerCreated, OnlineOrdersActionToggleLoading, OnlineOrdersPageToggleLoading } from "./onlineOrders.action";
import { QueryOnlineOrdersModel } from "./queryonlineOrders.model";

@Injectable()
export class OnlineOrdersEffect {
	showPageLoadingDistpatcher = new OnlineOrdersPageToggleLoading({ isLoading: true });
	hidePageLoadingDistpatcher = new OnlineOrdersPageToggleLoading({ isLoading: false });

	showActionLoadingDistpatcher = new OnlineOrdersActionToggleLoading({ isLoading: true });
	hideActionLoadingDistpatcher = new OnlineOrdersActionToggleLoading({ isLoading: false });

	@Effect()
	loadOnlineOrdersPage$ = this.actions$.pipe(
		ofType<OnlineOrdersPageRequested>(OnlineOrdersActionTypes.OnlineOrdersPageRequested),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showPageLoadingDistpatcher);
			const requestToServer = this.onlineOrders.getListOnlineOrders(payload.page);
			const lastQuery = of(payload.page);
			return forkJoin(requestToServer, lastQuery);
		}),
		map((response) => {
			let res: { errorMessage: string; totalCount: any; items: any };
			const result: QueryResultsModel = { items: response[0].data, totalCount: response[0].totalCount, errorMessage: "", data: [] };
			const lastQuery: QueryOnlineOrdersModel = response[1];
			return new OnlineOrdersPageLoaded({
				onlineOrders: result.items,
				totalCount: result.totalCount,
				page: lastQuery,
			});
		})
	);

	@Effect()
	deleteOnlineOrders$ = this.actions$.pipe(
		ofType<OnlineOrdersDeleted>(OnlineOrdersActionTypes.OnlineOrdersDeleted),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.onlineOrders.deleteOnlineOrders(payload.id);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	@Effect()
	updateOnlineOrders = this.actions$.pipe(
		ofType<OnlineOrdersUpdated>(OnlineOrdersActionTypes.OnlineOrdersUpdated),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.onlineOrders.updateOnlineOrders(payload.onlineOrders);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	@Effect()
	createBlock$ = this.actions$.pipe(
		ofType<OnlineOrdersOnServerCreated>(OnlineOrdersActionTypes.OnlineOrdersOnServerCreated),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.onlineOrders.createOnlineOrders(payload.onlineOrders).pipe(
				tap((res) => {
					this.store.dispatch(new OnlineOrdersCreated({ onlineOrders: res }));
				})
			);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	constructor(private actions$: Actions, private onlineOrders: OnlineOrdersService, private store: Store<AppState>) {}
}
