// Angular
import { Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
// Material
import { SelectionModel } from "@angular/cdk/collections";
import { MatPaginator, MatSort, MatSnackBar } from "@angular/material";
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay } from "rxjs/operators";
import { fromEvent, merge, Observable, of, Subscription } from "rxjs";
// LODASH
import { each, find } from "lodash";
// NGRX
import { Store, select } from "@ngrx/store";
import { AppState } from "../../../../core/reducers";

//services
import { LayoutUtilsService, MessageType, QueryParamsModel } from "../../../../core/_base/crud";

import { OnlineOrdersModel } from "../../../../core/onlineOrders/onlineOrders.model";
import { OnlineOrdersDeleted, OnlineOrdersPageRequested } from "../../../../core/onlineOrders/onlineOrders.action";
import { OnlineOrdersDataSource } from "../../../../core/onlineOrders/onlineOrders.datasource";
import { SubheaderService } from "../../../../core/_base/layout";
import { OnlineOrdersService } from "../../../../core/onlineOrders/onlineOrders.service";
import { HttpClient } from "@angular/common/http";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { QueryOnlineOrdersModel } from "../../../../core/onlineOrders/queryonlineOrders.model";

@Component({
	selector: "kt-list-onlineOrders",
	templateUrl: "./list-onlineOrders.component.html",
	styleUrls: ["./list-onlineOrders.component.scss"],
})
export class ListUnittypeComponent implements OnInit, OnDestroy {
	file;
	dataSource: OnlineOrdersDataSource;
	displayedColumns = ["name", "type", "time", "price", "status", "actions"];
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	@ViewChild("sort1", { static: true }) sort: MatSort;
	// Filter fields
	@ViewChild("searchInput", { static: true }) searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	// Selection
	selection = new SelectionModel<OnlineOrdersModel>(true, []);
	onlineOrdersResult: OnlineOrdersModel[] = [];
	data = localStorage.getItem("currentUser");
	dataUser = JSON.parse(this.data);
	onlineOrders = this.dataUser.role;
	// Subscriptions
	featureAccess = null;
	private subscriptions: Subscription[] = [];
	constructor(private activatedRoute: ActivatedRoute, private store: Store<AppState>, private router: Router, private service: OnlineOrdersService, private layoutUtilsService: LayoutUtilsService, private subheaderService: SubheaderService, private cdr: ChangeDetectorRef, private http: HttpClient, private modalService: NgbModal) {}

	ngOnInit() {
		const sortSubscription = this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));
		this.subscriptions.push(sortSubscription);
		/* Data load will be triggered in two cases:
		- when a pagination event occurs => this.paginator.page
		- when a sort event occurs => this.sort.sortChange
		**/
		const paginatorSubscriptions = merge(this.sort.sortChange, this.paginator.page)
			.pipe(
				tap(() => {
					this.loadOnlineOrdersList();
				})
			)
			.subscribe();
		this.subscriptions.push(paginatorSubscriptions);

		// Filtration, bind to searchInput
		const searchSubscription = fromEvent(this.searchInput.nativeElement, "keyup")
			.pipe(
				// tslint:disable-next-line:max-line-length
				debounceTime(50), // The user can type quite quickly in the input box, and that could trigger a lot of server requests. With this operator, we are limiting the amount of server requests emitted to a maximum of one every 150ms
				distinctUntilChanged(), // This operator will eliminate duplicate values
				tap(() => {
					this.paginator.pageIndex = 0;
					this.loadOnlineOrdersList();
				})
			)
			.subscribe();
		this.subscriptions.push(searchSubscription);

		// Set title to page breadCrumbs
		this.subheaderService.setTitle("Pesanan Online");

		// Init DataSource
		this.dataSource = new OnlineOrdersDataSource(this.store);
		const entitiesSubscription = this.dataSource.entitySubject.pipe(skip(1), distinctUntilChanged()).subscribe((res) => {
			this.onlineOrdersResult = res;
			console.log(res, "res");
		});
		this.subscriptions.push(entitiesSubscription);

		// First Load
		this.loadOnlineOrdersList();
	}

	filterConfiguration(): any {
		const filter: any = {};
		const searchText: string = this.searchInput.nativeElement.value.toLowerCase();

		filter.name = `${searchText}`;
		return filter;
	}

	loadOnlineOrdersList() {
		this.selection.clear();
		const queryParams = new QueryOnlineOrdersModel(this.filterConfiguration(), this.sort.direction, this.sort.active, this.paginator.pageIndex + 1, this.paginator.pageSize);
		this.store.dispatch(new OnlineOrdersPageRequested({ page: queryParams }));
		this.selection.clear();
	}

	deleteOnlineOrders(_item: OnlineOrdersModel) {
		// tslint:disable-next-line:variable-name
		const _title = "Pesanan Online Delete";
		// tslint:disable-next-line:variable-name
		const _description = "Are you sure to permanently delete this onlineOrders?";
		const _waitDesciption = "Pesanan Online is deleting...";
		const _deleteMessage = `Pesanan Online has been deleted`;

		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe((res) => {
			if (!res) {
				return;
			}

			this.store.dispatch(new OnlineOrdersDeleted({ id: _item._id }));
			this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
		});
	}

	isAllSelected(): boolean {
		const numSelected = this.selection.selected.length;
		const numRows = this.onlineOrdersResult.length;
		return numSelected === numRows;
	}

	masterToggle() {
		if (this.selection.selected.length === this.onlineOrdersResult.length) {
			this.selection.clear();
		} else {
			this.onlineOrdersResult.forEach((row) => this.selection.select(row));
		}
	}

	editOnlineOrders(id) {
		this.router.navigate(["edit", id], { relativeTo: this.activatedRoute });
	}

	viewOnlineOrders(id) {
		this.router.navigate(["view", id], { relativeTo: this.activatedRoute });
	}

	ngOnDestroy() {
		this.subscriptions.forEach((sb) => sb.unsubscribe());
	}

	export() {
		this.service.exportExcel();
	}
}
