import { Component, OnDestroy, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { FormBuilder, FormGroup, Validators, FormControl } from "@angular/forms";
// RxJS
import { BehaviorSubject, Observable, of, Subscription } from "rxjs";
// NGRX
import { Store, select } from "@ngrx/store";
import { Update } from "@ngrx/entity";
import { AppState } from "../../../../core/reducers";
// Layout
import { SubheaderService, LayoutConfigService } from "../../../../core/_base/layout";
import { LayoutUtilsService, MessageType } from "../../../../core/_base/crud";
import { OnlineOrdersModel } from "../../../../core/onlineOrders/onlineOrders.model";
import { selectLastCreatedOnlineOrdersId, selectOnlineOrdersActionLoading, selectOnlineOrdersById } from "../../../../core/onlineOrders/onlineOrders.selector";
import { OnlineOrdersService } from "../../../../core/onlineOrders/onlineOrders.service";

@Component({
	selector: "kt-view-onlineOrders",
	templateUrl: "./view-onlineOrders.component.html",
	styleUrls: ["./view-onlineOrders.component.scss"],
})
export class ViewOnlineOrdersComponent implements OnInit, OnDestroy {
	// Public properties
	onlineOrders: OnlineOrdersModel;
	OnlineOrdersId$: Observable<string>;
	oldOnlineOrders: OnlineOrdersModel;
	selectedTab = 0;
	loading$: Observable<boolean>;
	onlineOrdersForm: FormGroup;
	date1 = new FormControl(new Date());
	hasFormErrors = false;
	checker: boolean;
	loading: boolean;
	// Private properties
	private subscriptions: Subscription[] = [];
	constructor(private activatedRoute: ActivatedRoute, private router: Router, private onlineOrdersFB: FormBuilder, private subheaderService: SubheaderService, private layoutUtilsService: LayoutUtilsService, private store: Store<AppState>, private service: OnlineOrdersService, private layoutConfigService: LayoutConfigService) {}

	ngOnInit() {
		this.loading$ = this.store.pipe(select(selectOnlineOrdersActionLoading));
		const routeSubscription = this.activatedRoute.params.subscribe((params) => {
			const id = params.id;
			if (id) {
				this.store.pipe(select(selectOnlineOrdersById(id))).subscribe((res) => {
					if (res) {
						// this.checker = res.active;
						this.onlineOrders = res;
						this.oldOnlineOrders = Object.assign({}, this.onlineOrders);
						this.initOnlineOrders();
					}
				});
			}
		});
		this.subscriptions.push(routeSubscription);
	}

	initOnlineOrders() {
		this.createForm();
	}

	createForm() {
		if (this.onlineOrders._id) {
			this.onlineOrdersForm = this.onlineOrdersFB.group({
				// onlineOrdersCode: [{value:this.onlineOrders.onlineOrdersCode, disabled:true}],
				// onlineOrders: [{value:this.onlineOrders.onlineOrders, disabled:true}],
				// active: [{value:this.onlineOrders.active, disabled:true}],
				// max_login: [{value:this.onlineOrders.max_login, disabled:true}],
			});
		}
	}

	goBackWithId() {
		const url = `/onlineOrders`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	refreshOnlineOrders(isNew: boolean = false, id: string = "") {
		let url = this.router.url;
		if (!isNew) {
			this.router.navigate([url], { relativeTo: this.activatedRoute });
			return;
		}

		url = `/onlineOrders/edit/${id}`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	getComponentTitle() {
		let result = "View OnlineOrders";
		return result;
	}

	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	ngOnDestroy() {
		this.subscriptions.forEach((sb) => sb.unsubscribe());
	}
}
