// NGRX
import { createFeatureSelector, createSelector } from "@ngrx/store";
// CRUD
import { QueryResultsModel, HttpExtenstionsModel } from "../_base/crud";
// State
import { OnlineOrdersState } from "./onlineOrders.reducer";
import { each } from "lodash";
import { OnlineOrdersModel } from "./onlineOrders.model";

export const selectOnlineOrdersState = createFeatureSelector<OnlineOrdersState>("onlineOrders");

export const selectOnlineOrdersById = (onlineOrdersId: string) => createSelector(selectOnlineOrdersState, (onlineOrdersState) => onlineOrdersState.entities[onlineOrdersId]);

export const selectOnlineOrdersPageLoading = createSelector(selectOnlineOrdersState, (onlineOrdersState) => {
	return onlineOrdersState.listLoading;
});

export const selectOnlineOrdersActionLoading = createSelector(selectOnlineOrdersState, (onlineOrdersState) => onlineOrdersState.actionsloading);

export const selectLastCreatedOnlineOrdersId = createSelector(selectOnlineOrdersState, (onlineOrdersState) => onlineOrdersState.lastCreatedOnlineOrdersId);

export const selectOnlineOrdersPageLastQuery = createSelector(selectOnlineOrdersState, (onlineOrdersState) => onlineOrdersState.lastQuery);

export const selectOnlineOrdersInStore = createSelector(selectOnlineOrdersState, (onlineOrdersState) => {
	const items: OnlineOrdersModel[] = [];
	each(onlineOrdersState.entities, (element) => {
		items.push(element);
	});
	const httpExtension = new HttpExtenstionsModel();
	const result: OnlineOrdersModel[] = httpExtension.sortArray(items, onlineOrdersState.lastQuery.sortField, onlineOrdersState.lastQuery.sortOrder);
	return new QueryResultsModel(result, onlineOrdersState.totalCount, "");
});

export const selectOnlineOrdersShowInitWaitingMessage = createSelector(selectOnlineOrdersState, (onlineOrdersState) => onlineOrdersState.showInitWaitingMessage);

export const selectHasOnlineOrdersInStore = createSelector(selectOnlineOrdersState, (queryResult) => {
	if (!queryResult.totalCount) {
		return false;
	}

	return true;
});
