// NGRX
import { createFeatureSelector } from "@ngrx/store";
import { EntityState, EntityAdapter, createEntityAdapter } from "@ngrx/entity";
// Actions
import { OnlineOrdersActions, OnlineOrdersActionTypes } from "./onlineOrders.action";
// CRUD

// Models
import { OnlineOrdersModel } from "./onlineOrders.model";
import { QueryOnlineOrdersModel } from "./queryonlineOrders.model";

// tslint:disable-next-line:no-empty-interface
export interface OnlineOrdersState extends EntityState<OnlineOrdersModel> {
	listLoading: boolean;
	actionsloading: boolean;
	totalCount: number;
	lastCreatedOnlineOrdersId: string;
	lastQuery: QueryOnlineOrdersModel;
	showInitWaitingMessage: boolean;
}

export const adapter: EntityAdapter<OnlineOrdersModel> = createEntityAdapter<OnlineOrdersModel>({ selectId: (model) => model._id });

export const initialOnlineOrdersState: OnlineOrdersState = adapter.getInitialState({
	listLoading: false,
	actionsloading: false,
	totalCount: 0,
	lastQuery: new QueryOnlineOrdersModel({}),
	lastCreatedOnlineOrdersId: undefined,
	showInitWaitingMessage: true,
});

export function onlineOrdersReducer(state = initialOnlineOrdersState, action: OnlineOrdersActions): OnlineOrdersState {
	switch (action.type) {
		case OnlineOrdersActionTypes.OnlineOrdersPageToggleLoading:
			return {
				...state,
				listLoading: action.payload.isLoading,
				lastCreatedOnlineOrdersId: undefined,
			};
		case OnlineOrdersActionTypes.OnlineOrdersActionToggleLoading:
			return {
				...state,
				actionsloading: action.payload.isLoading,
			};
		case OnlineOrdersActionTypes.OnlineOrdersOnServerCreated:
			return {
				...state,
			};
		case OnlineOrdersActionTypes.OnlineOrdersCreated:
			return adapter.addOne(action.payload.onlineOrders, {
				...state,
				lastCreatedBlockId: action.payload.onlineOrders._id,
			});
		case OnlineOrdersActionTypes.OnlineOrdersUpdated:
			return adapter.updateOne(action.payload.partialOnlineOrders, state);
		case OnlineOrdersActionTypes.OnlineOrdersDeleted:
			return adapter.removeOne(action.payload.id, state);
		case OnlineOrdersActionTypes.OnlineOrdersPageCancelled:
			return {
				...state,
				listLoading: false,
				lastQuery: new QueryOnlineOrdersModel({}),
			};
		case OnlineOrdersActionTypes.OnlineOrdersPageLoaded: {
			return adapter.addMany(action.payload.onlineOrders, {
				...initialOnlineOrdersState,
				totalCount: action.payload.totalCount,
				lastQuery: action.payload.page,
				listLoading: false,
				showInitWaitingMessage: false,
			});
		}
		default:
			return state;
	}
}

export const getOnlineOrdersState = createFeatureSelector<OnlineOrdersState>("onlineOrders");

export const { selectAll, selectEntities, selectIds, selectTotal } = adapter.getSelectors();
